* {
  margin: 0;
  padding: 0;
}
.logo_outer {
  height: 100px;
  width: 100vw;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.logo {
  position: absolute;
  height: 50px;
}
.logo path {
  fill: transparent;
  stroke-width: 50;
  stroke: black;
  fill: black;
  stroke-dasharray: 4500;
  stroke-dashoffset: 0;
}
.logo:hover path {
  animation: animate-heart 3s linear forwards;
}
@keyframes animate-heart {
  0% {
    fill: transparent;
    stroke-dashoffset: 0;
    stroke: rgb(60, 157, 243);
  }
  30% {
    stroke-dashoffset: 3000;
    stroke: red;
  }
  60% {
    stroke-dashoffset: 5750;
    stroke: green;
    fill: transparent;
  }
  90% {
    stroke-dashoffset: 9000;
    stroke: orange;
    fill: transparent;
  }
  100% {
    stroke-dashoffset: 9000;
    fill: black;
  }
}
