* {
  margin: 0;
  padding: 0;
}
.outer {
  width: 100px;
  height: 100px;
  background-image: url("./center.png");
  background-repeat: no-repeat;
  background-position: center center;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  translate: 99px;
}
.table {
  height: 100px;
  width: 100px;
  border: 0px solid black;
}
.td {
  width: 30px;
  text-align: center;
  vertical-align: middle;
  border: 0px;
}
tr {
  height: 30px;
}
.wid {
  height: 26px;
  width: 26px;
}
@media screen and (max-width: 510px) {
  .outer {
    width: 69px;
    height: 69px;
    translate: 71px;
  }
  .table {
    height: 70px;
    width: 70px;
  }
  .td {
    width: 21px;
  }
  tr {
    height: 21px;
  }
  .wid {
    height: 18px;
    width: 18px;
  }
}
