* {
  margin: 0;
  padding: 0;
}
.rest_table {
  height: 199px;
  width: 99px;
}
table {
  border: 1px solid;
}
td {
  width: 30px;
  border: 1px solid black;
  text-align: center;
  vertical-align: middle;
}
tr {
  height: 30px;
  border: 1px solid black;
}
.star {
  position: absolute;
  margin: 0px;
  height: 25px;
  width: 25px;
  transform: rotate(180deg);
  clip-path: polygon(
    50% 0%,
    61% 35%,
    98% 35%,
    68% 57%,
    79% 91%,
    50% 70%,
    21% 91%,
    32% 57%,
    2% 35%,
    39% 35%
  );
}
.arrow {
  position: absolute;
  margin: 0px;
  height: 25px;
  width: 30px;
  transform: rotate(90deg);
  clip-path: polygon(
    0% 20%,
    60% 20%,
    60% 0%,
    100% 50%,
    60% 100%,
    60% 80%,
    0% 80%
  );
}
.wid {
  height: 26px;
  width: 26px;
  background-color: transparent;
}
.b {
  background-color: rgb(60, 157, 243);
}
.g {
  background-color: green;
}
.o {
  background-color: orange;
}
.r {
  background-color: red;
}
@media screen and (max-width: 510px) {
  .rest_table {
    height: 139px;
    width: 69px;
  }
  td {
    width: 21px;
    border: 0.7px solid black;
  }
  tr {
    height: 21px;
    border: 0.7px solid black;
  }
  .star {
    height: 17px;
    width: 17px;
  }
  .arrow {
    height: 17px;
    width: 21px;
  }
  .wid {
    height: 18px;
    width: 18px;
  }
  table {
    border: 0.7px solid;
  }
}
