* {
  margin: 0;
  padding: 0;
}
.outer_main_box {
  height: 100vh;
  width: 100vw;
  position: absolute;
  top: 39px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.box {
  width: 665px;
  height: 510px;
  display: flex;
}
.box1 {
  height: 500px;
  width: 500px;
  border: 5px solid black;
}
.box2 {
  height: 500px;
  width: 150px;
  background-color: transparent;
  border-top: 5px solid black;
  border-right: 5px solid black;
  border-bottom: 5px solid black;
  display: flex;
  justify-content: center;
  align-items: center;
}
.upper {
  display: flex;
  flex-direction: row;
}
.mid {
  display: flex;
  flex-direction: row;
  height: 100px;
}
.lower {
  display: flex;
  flex-direction: row;
}
.rot90 {
  transform-origin: top right;
  translate: -100px;
  transform: rotate(-90deg);
}
.rot-90 {
  transform-origin: top right;
  transform: rotate(90deg);
  translate: 200px 100px;
}
.rot180 {
  transform: rotate(180deg);
}
@media screen and (max-width: 670px) {
  .box {
    flex-direction: column;
    height: 665px;
    width: 510px;
  }
  .box2 {
    height: 150px;
    width: 500px;
    border-top: 0px;
    border-left: 5px solid black;
  }
  .outer_main_box {
    top: 100px;
  }
}
@media screen and (max-width: 510px) {
  .box {
    height: 465px;
    width: 350px;
  }
  .outer_main_box {
    top: 25px;
  }
  .box1 {
    height: 350px;
    width: 350px;
  }
  .box2 {
    height: 106px;
    width: 350px;
  }
  .mid {
    height: 70px;
  }
  .rot90 {
    translate: -70px;
  }
  .rot-90 {
    translate: 142px 70px;
  }
  .outer_center {
    translate: 70px;
  }
}
